import Grid from "@material-ui/core/Grid";
import { makeStyles } from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import InputLabel from "@material-ui/core/InputLabel";
import Typography from "@material-ui/core/Typography";
import {
  KeyboardDatePicker,
  MuiPickersUtilsProvider,
} from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";
import React from "react";

const useStyles = makeStyles((theme) => ({
  texField: {
    width: "50%",
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 180,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));

export default function GeneralInfoForm({
  application,
  errors,
  onChange,
  onFlightDateChange,
  onTestDateChange,
}) {
  const classes = useStyles();
  return (
    <React.Fragment>
      <Typography variant="h6" gutterBottom>
          
      </Typography>
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <TextField
            className={classes.texField}
            name="name1"
            error={errors.name1}
            label=" "
            onChange={onChange}
            value={application.name1}
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            className={classes.texField}
            name="name2"
            error={errors.name2}
            label=" "
            onChange={onChange}
            value={application.name2}
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            className={classes.texField}
            name="name3"
            error={errors.name3}
            label=" "
            onChange={onChange}
            value={application.name3}
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            className={classes.texField}
            name="name4"
            error={errors.name4}
            label=" "
            onChange={onChange}
            value={application.name4}
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            className={classes.texField}
            name="ename1"
            error={errors.ename1}
            label="   "
            onChange={onChange}
            value={application.ename1}
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            className={classes.texField}
            name="ename2"
            error={errors.ename2}
            label="   "
            onChange={onChange}
            value={application.ename2}
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            className={classes.texField}
            name="ename3"
            error={errors.ename3}
            label="   "
            onChange={onChange}
            value={application.ename3}
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            className={classes.texField}
            name="ename4"
            error={errors.ename4}
            label="   "
            onChange={onChange}
            value={application.ename4}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            className={classes.texField}
            name="destination"
            error={errors.destination}
            label=" "
            value={application.destination}
            onChange={onChange}
          />
        </Grid>
        <Grid dir="rtl" item xs={12}>
          <FormControl className={classes.formControl}>
            <InputLabel id="airlines"> </InputLabel>
            <Select
              labelId="airlines"
              name="airlines"
              value={application.airlines}
              onChange={onChange}
            >
              <MenuItem value="Badr"></MenuItem>
              <MenuItem value="Tarko"></MenuItem>
              <MenuItem value="Eithiopian"></MenuItem>
              <MenuItem value="Turky"></MenuItem>
              <MenuItem value="Fly Dubai"> </MenuItem>
              <MenuItem value="Qatar"></MenuItem>
              <MenuItem value="Fly Emarits"></MenuItem>
              <MenuItem value="Itihad"></MenuItem>
              <MenuItem value="Nas">  </MenuItem>
              <MenuItem value="Saudi"></MenuItem>
              <MenuItem value="other"></MenuItem>
            </Select>
          </FormControl>
        </Grid>
        {application.airlines === "other" && (
          <Grid item xs={12} sm={12}>
            <TextField
              className={classes.texField}
              name="otherAirlines"
              error={errors.otherAirlines}
              label=" "
              onChange={onChange}
              value={application.otherAirlines}
            />
          </Grid>
        )}
        <Grid item xs={12} sm={12}>
          <TextField
            className={classes.texField}
            name="phoneNumber"
            error={errors.phoneNumber}
            label=" "
            onChange={onChange}
            value={application.phoneNumber}
          />
          <small>     , : 9123000000</small>
        </Grid>
        <Grid item xs={12} sm={12}>
          <TextField
            className={classes.texField}
            name="passportNumber"
            error={errors.passportNumber}
            label=" "
            onChange={onChange}
            value={application.passportNumber}
          />
        </Grid>

        <Grid item xs={12}>
          <MuiPickersUtilsProvider utils={DateFnsUtils}>
            <div
              style={{
                display: "flex",
                alignItems: "center",
              }}
            >
              <KeyboardDatePicker
                variant="inline"
                format="dd/MM/yyyy"
                margin="normal"
                id="date-picker-inline"
                disablePast
                label=" "
                value={application.flightDate}
                onChange={onFlightDateChange}
                KeyboardButtonProps={{
                  "aria-label": "change date",
                }}
              />
              <span
                style={{
                  fontSize: 12,
                  marginRight: 20,
                  marginTop: 35,
                  color: "gray",
                }}
              >
                    
              </span>
            </div>
          </MuiPickersUtilsProvider>
        </Grid>

        <Grid item xs={12}>
          <MuiPickersUtilsProvider utils={DateFnsUtils}>
            <div
              style={{
                display: "flex",
                alignItems: "center",
              }}
            >
              <KeyboardDatePicker
                variant="inline"
                format="dd/MM/yyyy"
                margin="normal"
                id="date-picker-inline"
                disablePast
                label=" "
                value={application.testDate}
                onChange={onTestDateChange}
                KeyboardButtonProps={{
                  "aria-label": "change date",
                }}
              />
              <span
                style={{
                  fontSize: 12,
                  marginRight: 20,
                  marginTop: 35,
                  color: "gray",
                }}
              >
                    
              </span>
            </div>
          </MuiPickersUtilsProvider>
        </Grid>
      </Grid>
    </React.Fragment>
  );
}
